/* ORANGE COLOR SCHEME */
.main.orange .sidebar_menu-item-project:hover,
.main.orange .sidebar_menu-item-project.active
{
    background-color: #FF4F0020;
}
.main.orange .sidebar-logo-image-item-nosquare
{
    border-top: 3px solid #FF4F00;
    border-left: 3px solid #FF4F00;
    border-right: 3px solid #FF4F00;
}
.main.orange .login-logo-image-item-nosquare {
    border-top: 6px solid #FF4F00;
    border-left: 6px solid #FF4F00;
    border-right: 6px solid #FF4F00;
}
.main.orange .sidebar-logo-image-item-line {
    background-color: #FF4F00;
}
.main.orange .login-logo-image-item-line {
    background-color: #FF4F00;
}
.main.orange .sidebar-logo-image-item-square {
    border: 3px solid #FF4F00;
}
.main.orange .login-logo-image-item-square {
    border: 6px solid #FF4F00;
}
.main.orange .sidebar-logo-text {
    color: #FF4F00;
}
.main.orange .login-logo-text {
    color: #FF4F00;
}
.main.orange .sidebar-item-block:hover,
.main.orange .sidebar-item-block.active
{
    background-color: #FF4F0020;
}
.main.orange #button-login {
    background-color: #FF4F00;
}
.main.light.orange .sidebar_settings-menu-item.active {
    background-color: #FF4F0040;
}
.main.dark.orange .sidebar_settings-menu-item.active {
    background-color: #FF4F0080;
}
.main.orange .table-cols-item.active {
    background-color: #FF4F00;
}
.main.orange .table-cols-item {
    background-color: #FF4F0080;
}
.main.orange .table-body-row.selected {
    background-color: #FF4F0030;
}


