.main {
  display: flex;
  height: 100vh;
  width: 100%;
  overflow: hidden;
  position: relative;
}
#btn-quit {
  position: absolute;
  top: 10px;
  right: 20px;
  width: 20px;
  height: 20px;
  background-color: transparent;
  border: none;
}
#btn-quit img {
  width: 20px;
  height: 20px;
  opacity: .5;
}
#hidden-sidebar {
  position: absolute;
  width: 40px;
  height: 40px;
  overflow: hidden;
  border-radius: 50%;
  border: none;
  display: none;
  justify-content: center;
  align-items: center;
  z-index: 1020;
}
#hidden-sidebar img {
  width: 20px;
  height: 20px;
  opacity: .5;
}
#btn-settings-mobile {
  width: 20px;
  height: 30px;
  border: none;
  background-color: transparent;
  display: none;
  justify-content: center;
  align-items: center;
  position: absolute;
  z-index: 101;
}
#btn-settings-mobile img {
  width: 20px;
  height: 20px;
  opacity: .5;
}
.sidebar-userName-dataBase {
  display: flex;
  flex-direction: column;
  gap: 5px;
  width: 100%;
  justify-content: center;
}
.sidebar-name-dataBase {
  width: 100%;
  text-align: center;
  font-size: 14px;
}
#button-back {
  position: absolute;
  top: 40px;
  left: 20px;
  padding: 5px 10px;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  width: 40px;
  height: 25px;
}
.sidebar.settings #button-back {
  top: 40px;
  right: auto;
  left: 10px;
}
#button-back img {
  width: 20px;
  height: 15px;
}

@media ( max-width: 767px ) {
  #hidden-sidebar {
    right: 20px;
    display: flex;
  }
  .sidebar.hidden {
    overflow: visible;
    transition: all .3s ease;
    transform: translateX(-100%);
  }
  .sidebar:not(.hidden) {
    transition: all .3s ease;
    transform: translateX(0%);
  }
  .sidebar.hidden #hidden-sidebar {
    right: -60px;
  }
  .resize-handle {
    display: none;
  }
  #btn-settings-mobile {
    width: 30px;
    height: 30px;
    left: 20px;
    top: 40px;
    display: flex;
  }
  #btn-settings-mobile img {
    width: 30px;
    height: 30px;
  }
  .sidebar #button-back {
    top: 40px;
    right: 70px;
    left: auto;
    width: 45px;
    height: 30px;
  }
  #button-back img {
    width: 25px;
    height: 20px;
  }
  #btn-quit {
    top: 20px;
    width: 30px;
    height: 30px;
  }
  #btn-quit img {
    width: 30px;
    height: 30px;
  }
}