/* LIGHT THEME */
.main.light {
    background-color: #EFEFEF;
    color: #000;
}
.main.light .sidebar,
.main.light .settings_container-content,
.main.light .login_content form,
.main.light .sidebar_menu-item-project-link,
.main.light .main_content-table,
.main.light .main_content-panel
{
    background-color: #fff;
}
.main.light.blur .sidebar,
.main.light.blur .settings_container-content,
.main.light.blur .login_content form,
.main.light.blur .sidebar_menu-item-project-link,
.main.light.blur .main_content-table,
.main.light.blur .main_content-panel
{
    background-color: #fff5;
    backdrop-filter: blur(20px);
}

.main.light .login_content form h1,
.main.light .sidebar_menu-item-project span,
.main.light .sidebar-item-block span
{
    color: #000;
}
.main.light .setting-profile-image-image,
.main.light .setting-profile-image button,
.main.light .sidebar-user-image
{
    background-color: #0001;
    color: #000;
}
.main.light .support-content-link {
    border: 1px solid #0005;
}
.main.light .support-content-link a {
    color: #0005;
}
.main.light .contact-item:not(.title) span {
    color: #0005;
}
.main.light ::-webkit-scrollbar {
    background-color: #0002;
    width: 7px;
    border-radius: 10px;
}
.main.light ::-webkit-scrollbar-thumb {
    background-color: #0005;
    border-radius: 10px;
}
.main.light .login-inputs {
    color: #000;
    background-color: #0001;
}
.main.light .sidebar-panel-item-btn
{
    background-color: #0001;
}
.main.light .list-btn,
.main.light .select-list,
.main.light #btn-search
{
    background-color: #fff8;
    color: #000;
}
.main.light .setting-more-text {
    color: #0005;
}
.main.light #btn-main-panel {
    background-color: #00000015;
}
.main.light #hidden-sidebar {
    background-color: #fff;
    box-shadow: 0 0 4px #0002;
}
.main.light .setting-item-card-text select,
.main.light .setting-item-card-text:not(.toggle-btn) button
{
    background-color: #FFF0F0;
    box-shadow: 0 4px 4px 0 #00000025;
    color: #000;
}
.main.light .sidebar-name-dataBase {
  color: #0005;
}
.main.light .main_content-header-item-title {
    color: #000;
}
.main.light .main_content-header-item-title.invert-text {
    color: #fff;
}
.main.light .modal-add-list-content,
.main.light .modal-edit-list-content,
.main.light .modal-delete-list-content
{
    background-color: #FFF0F0;
    box-shadow: 0 0 4px #0005;
}
.main.light #new-list,
.main.light #edit-all-list,
.main.light #edit-list,
.main.light #old-list
{
    color: #000;
}
.main.light .old-list-all,
.main.light .edit-all
{
    background-color: #fff;
    box-shadow: 0 4px 4px #0005;
}
.main.light .error-modal,
.main.light #button-back 
{
    background-color: #fff;
    box-shadow: 0 4px 4px #0002;
}


/* DARK THEME */
.main.dark {
    background-color: #222222;
    color: #fff;
}
.main.dark .sidebar,
.main.dark .settings_container-content,
.main.dark .login_content form,
