.sidebar-panel {
    width: 100%;
    height: 25px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 5px 0 15px;
}
.sidebar-panel-item {
    display: flex;
    gap: 5px;
    align-items: center;
}
.sidebar-panel-item-btn {
    overflow: hidden;
    width: 25px;
    height: 25px;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
}
.sidebar-panel-item-btn img {
    width: 15px;
    opacity: .5;
}
.sidebar-panel-item-btn.favorites .fav1 {
    display: block;
}
.sidebar-panel-item-btn.favorites .fav2 {
    display: none;
    opacity: 1;
}
ul, li {
    width: 100%;
}
.sidebar-item-block {
    background-color: transparent;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px 10px;
    border-radius: 10px;
    cursor: pointer;
    position: relative;
}
.sidebar-item-block span {
    font-size: 16px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.sidebar-item-block img {
    width: 12px;
    height: 6px;
    transition: all .3s ease;
}
ul {
    display: flex;
    flex-direction: column;
    gap: 5px;
    list-style: none;
    list-style-type: none;
    padding-left: 0;
}
.sidebar-menu ul ul {
    /* padding-left: 20px; */
    transition: all .3s ease;
}
summary {
    list-style: none;
    margin-bottom: 5px;
}
.group-list {
    font-weight: 600;
}
.main_content {
    flex: 1;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    padding: 35px 20px 0;
    gap: 20px;
}
.main_content-header {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.main_content-header.home {
    justify-content: right;
}
.main_content-header-item {
    display: flex;
    gap: 10px;
}
.main_content-header-item-line {
    width: 1px;
    height: 20px;
    background-color: #0002;
}
.main_content-header-item-title {
    font-size: 16px;
    width: 100%;
    flex: 1;
}
.list {
    display: flex;
    gap: 2px;
    align-items: center;
}
.list label {
    margin-right: 8px;
}
.select-list {
    width: 140px;
    height: 30px;
    /* height: 4.1vh; */
    font-size: 14px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}
.select-list option {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    width: 140px !important;
}
.list-btns {
    display: flex;
    align-items: center;
    gap: 2px;
}
.list-btn {
    border-radius: 5px;
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
}
.list-btn img {
    width: 15px;
    height: 15px;
}
.main-btns {
    display: flex;
    gap: 15px;
    align-items: center;
}
.btn-search {
    width: 30px;
    height: 30px;
    overflow: hidden;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
    padding: 5px;
    gap: 10px;
    transition: all .3s ease;
}
.btn-search img {
    width: 17px;
    height: 17px;
    opacity: .5;
}
#input-search {
    width: 100%;
    height: 17px;
    border: none;
    outline: none;
    background-color: transparent;
    display: none;
    opacity: 0;
    transition: all .3s ease;
    font-size: 16px;
}
#input-search::placeholder {
    font-size: 16px;
}
.btn-search.active {
    width: 150px;
    justify-content: left;
}
.btn-search.active #input-search {
    display: flex;
    opacity: .5;
}


#btn-favorite,
#btn-settings {
    width: 20px;
    height: 30px;
    border: none;
    background-color: transparent;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}
#btn-all-docs-mobile {
    width: 20px;
    height: 30px;
    position: absolute;
    left: 20px;
    top: 10px;
    z-index: 101;
    display: none;
}
#btn-all-docs-mobile img {
    width: 20px;
    height: 20px;
    opacity: .5;
}
.btn-favorite-star-nofav,
.btn-favorite-star-fav
{
    width: 20px;
    height: 20px;
    position: absolute;
    transition: all .3s ease;
}
.btn-favorite-star-nofav {
    opacity: .5;
}
.btn-favorite-star-fav {
    opacity: 0;
}
@media (hover :hover) {
    #btn-favorite:hover .btn-favorite-star-nofav {
        opacity: 0;
    }
    #btn-favorite:hover .btn-favorite-star-fav {
        opacity: 1;
    }
}
@media (hover :none) {
    #btn-favorite:active .btn-favorite-star-nofav {
        opacity: 0;
    }
    #btn-favorite:active .btn-favorite-star-fav {
        opacity: 1;
    }
}
/* #btn-favorite:hover .btn-favorite-star-nofav {
    opacity: 0;
}
#btn-favorite:hover .btn-favorite-star-fav {
    opacity: 1;
} */
#btn-settings img {
    width: 20px;
    height: 20px;
}
.main_content-table {
    width: 100%;
    height: 100%;
    border-radius: 10px;
    overflow: auto;
}
.main_content-table table {
    position: relative;
    width: 100%;
    table-layout: auto;
    border-collapse: collapse;
}
tr {
    display: flex;
    gap: 0;
}
.main_content-table table thead tr th,
.main_content-table table tbody tr td {
    width: 100%;
    padding: 5px 10px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    max-width: 400px;
    text-align: center;
    position: relative;
}
.main_content-table table thead tr th:not(:last-child)::before {
    content: '';
    position: absolute;
    right: 0;
    width: 1px;
    height: 50%;
}
.cell-row-small {
    max-width: 100px !important;
    /* width: 100%; */
}
.main_content-table table thead tr {
    position: sticky;
    left: 0;
    right: 0;
    top: 0;
}
.main_content-table table thead tr,
.main_content-table table tbody tr {
    /* display: flex;
    gap: 0; */
    width: 100%;
}
.main_content-table td {
    cursor: pointer;
}
#table-body {
    position: relative;
}
.col-star {
    width: 30px !important;
    padding: 5px 15px !important;
    display: flex;
    justify-content: center;
    align-items: center;
}
.col-star img {
    width: 15px;
}
.fav-star-in-table.thead {
    cursor: pointer;
}
.fav-star-in-table {
    width: 15px;
    height: 15px;
}
.main_content-panel {
    height: 20px;
    width: 100%;
    flex-direction: column;
    border-radius: 10px;
    padding: 2px 20px;
    transition: all .3s ease;
}
.main_content-panel.novisible-panel {
    display: none;
}
.main_content-panel.visible-panel {
    display: flex;
}
.main_content-panel.active {
    height: 205px;
}
.main_content-panel-header {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
}
.main_content-panel-header-item {
    display: flex;
    gap: 10px;
}
.main_content-panel-header-item:nth-child(1) {
    justify-content: left;
}
.main_content-panel-header-item:nth-child(2) {
    justify-content: center;
}
.main_content-panel-header-item:nth-child(3) {
    justify-content: right;
}
.main_content-panel-header-item-title {
    background-color: #0002;
    font-size: 12px;
    padding: 1px 10px;
    border-radius: 2px;
    margin-bottom: 5px;
    cursor: pointer;
    transition: all .3s ease;
    user-select: none;
}
.main_content-panel-header-item-title.active {
    margin-bottom: 0px;
    height: 100%;
    background-color: #0001;
}
.main_content-panel.active {
    padding: 5px 20px;
}
.main_content-panel.active #btn-main-panel img {
    transform: rotate(180deg);
}
#btn-main-panel {
    width: 35px;
    height: 15px;
    border-radius: 2px;
    border: none;
    overflow: hidden;
}
#btn-main-panel img {
    width: 12px;
    height: 7px;
    transform: rotate(0);
}

.main_content-panel-content {
    background-color: #0001;
    width: 100%;
    height: 100%;
    border-radius: 10px;
    overflow: hidden;
}
.main_content-panel-content.history {
    border-radius: 0 10px 10px 10px;
}

.main_content-panel-content-item {
    display: none;
}
.main_content-panel-content-item.active {
    display: flex;
    width: 100%;
    height: 100%;
    overflow: auto;
    height: 130px;
}
.main_content-panel-content-item table {
    position: relative;
    width: 100%;
}
.main_content-panel-content-item table tbody tr {
    cursor: pointer;
}
.main_content-panel-content-item.three {
    padding: 10px;
}
.main_content-panel-content-item table thead tr th,
.main_content-panel-content-item table tbody tr td {
    padding: 5px 25px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    max-width: 100%;
}
.main_content-panel-content-item table thead tr {
    position: sticky;
    left: 0;
    right: 0;
    top: 0;
}
.main_content-panel-content-item table thead tr,
.main_content-panel-content-item table tbody tr {
    width: 100%;
    display: flex;
    gap: 0;
}
thead tr th {
    position: relative;
}
thead tr th:not(:last-child)::before {
    content: '';
    position: absolute;
    right: 0;
    width: 1px;
    height: 60%;
}

.modal-add-list,
.modal-edit-list,
.modal-delete-list
{
