    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 100;
    /* background-color: #0001; */
    justify-content: center;
    align-items: center;
    display: flex;
    padding: 20px;
}
.modal-add-list-content,
.modal-edit-list-content,
.modal-delete-list-content
{
    /* background-color: #FFF0F0; */
    border-radius: 10px;
    padding: 20px;
    /* box-shadow: 0 0 4px #0005; */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 12px;
    max-width: 376px;
    width: 100%;
}
.modal-title,
.modal-text {
    width: 100%;
    text-align: center;
}
.modal-title {
    font-size: 18px;
    font-weight: 600;
}
.modal-text {
    font-size: 16px;
    font-weight: 300;
}
.modal-input {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: baseline;
    position: relative;
}
.modal-input-all-list,
.modal-input-edit-list {
    display: flex;
    flex-direction: column;
    gap: 10px;
    width: 100%;
    justify-content: center;
    align-items: baseline;
    position: relative;
}
