}
#modal-btn-add-list,
#modal-btn-edit-list,
#modal-btn-delete-list
{
    background-color: #FF4F00;
    color: #fff;
}
.error-modal {
    position: fixed;
    top: 50px;
    right: 20px;
    z-index: 200;
    border-radius: 10px;
    padding: 10px 20px;
    max-width: 220px;
    transform: translateX(300px);
    transition: all .3s ease;
}
.error-modal.active {
    animation: popInOut 3s forwards;
}
@keyframes popInOut {
    0% {
        transform: translateX(300px); /* Начальная позиция */
    }
    10% {
        transform: translateX(-10px); /* Ударяемся влево */
    }
    20% {
        transform: translateX(0); /* Возвращаемся на 0 */
    }
    80% {
        transform: translateX(0); /* Возвращаемся на 0 */
    }
    90% {
        transform: translateX(-10px); /* Ударяемся влево */
    }
    100% {
        transform: translateX(300px); /* Убираемся вправо */
    }
}
.error-modal-content {
    padding: 0 10px;
    position: relative;
    display: flex;
    flex-direction: column;
    gap: 10px;
    width: 100%;
}
.error-modal-content::before {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    width: 2px;
    height: 100%;
    background-color: rgb(247, 89, 41);
}
.error-modal-title,
.error-modal-text {
    width: 100%;
}
.error-modal-title {
    color: rgb(247, 89, 41);
    font-size: 16px;
}
.error-modal-text {
    font-size: 14px;
}



@media ( max-width: 767px ) {
    .sidebar.all_doc.hidden #hidden-sidebar {
        right: -60px;
    }
    .main_content-header {
        flex-direction: column;
        gap: 10px;
    }
    .main_content-header-item:not(.two) {
        width: 100%;
        padding: 0 0 0 50px;
        align-items: center;
        min-height: 40px;
    }
    .main_content-header-item-title {
        width: 100%;
    }
    .list {
        flex-direction: column;
        width: 100%;
    }
    .list-label {
        width: 100%;
        display: flex;
        align-items: center;
        gap: 5px;
    }
    .list-label select {
        width: 100%;
    }
    .list-btns {
        width: 100%;
        justify-content: left;
    }
    .list-btn {
        width: 100%;
    }
    .main_content-header-item.two {
        width: 100%;
        justify-content: space-between;
        gap: 20px;
    }
    .main-btns {
        gap: 10px;
    }
    .btn-search {
        width: 35px;
        height: 35px;
    }
    .btn-search img {
        width: 20px;
        height: 20px;
    }
    #btn-favorite,
    #btn-settings {
        width: 25px;
        height: 35px;
    }
    #btn-favorite img,
    #btn-settings img {
        width: 25px;
        height: 25px;
    }
    .sidebar {
        height: 100%;
    }
    .sidebar-menu {
        height: 100%;
        overflow: auto;
    }
}